<template>
  <div class="customer-information">
    <div class="customer-header">
      <PublicHead title="客户信息" :isBtn="false" />
    </div>
    <div class="customer-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="isMore" infinite-scroll-distance="5">
      <div class="customer-item" v-for="item in contactList" :key="item._id">
        <div class="customer-time">
          {{item.time}}
        </div>
        <div class="customer-main">
          <div class="visiting-card">
            <img :src="item.callCard" alt="">
          </div>
          <div class="intention">
            <div class="intention-item">
              <span class="intention-title">
                采购意向
              </span>
              <span>
                {{item.plan}}
              </span>
            </div>
            <div class="intention-item">
              <span class="intention-title">
                沟通意向
              </span>
              <span>
                {{item.intention}}
              </span>
            </div>
          </div>
        </div>
      </div>
      <!-- 下拉提示 -->
      <div class="course-down">
        <p v-show="tipsIndex === 1" class="course-down-tips">加载中，请稍后</p>
        <p v-show="tipsIndex === 2" class="course-down-tips">上拉加载更多</p>
        <p v-show="tipsIndex === 3" class="course-down-tips">已显示全部内容</p>
      </div>
    </div>
  </div>
</template>

<script>
import PublicHead from '../../components/publicHead.vue'
export default {
  data() {
    return {
      pageIndex: 0,  //当前页
      pageSize: 10,  //一页多少条 

      contactList: [],
      isMore: false,
      tipsIndex: 2,
    }
  },
  methods: {
    loadMore() {
      this.loading = true;
      this.tipsIndex = 1;
      this.pageIndex++

      this.getContact()
    },
    async getContact() {
      try {
        let res = await this.$api.staffOnly.getContact({
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        })

        console.log(res);
        if (res.statusCode === 75200) {
          this.contactList.push(...res.list);
          if (res.list.length < this.pageSize) {
            this.isMore = true;
            this.tipsIndex = 3;
          } else {
            this.isMore = false;
          }
        } else {
          this.$zero.Toast('服务器异常，请稍后重试')
        }

      } catch (err) {
        console.log(err);
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    }
  },
  components: {
    PublicHead
  }
}
</script>

<style scoped>
.customer-information {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: url("../../assets/img/contactUs/bg_popup.png") no-repeat;
  background-size: 100% 100%;
}
.customer-header {
  /* padding: .7rem 0 .3rem .4rem; */
  /* padding: 0.4rem 0.2rem .3rem 0.4rem; */
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #336a82;
}
.customer-list {
  width: 100%;
  box-sizing: border-box;
  padding: 0.3rem 0.2rem 0;
}
.customer-item {
  width: 100%;
  background: linear-gradient(
    to right,
    rgba(0, 179, 255, 0.25) 0%,
    rgba(0, 255, 213, 0.25) 100%
  );
  margin-bottom: 0.25rem;
}
.customer-time {
  width: 100%;
  height: 0.5rem;
  box-sizing: border-box;
  line-height: 0.5rem;
  padding-left: 0.25rem;
  font-size: 0.24rem;
  color: #fff;
  border-bottom: 1px solid #008cfc;
}
.customer-main {
  width: 100%;
  box-sizing: border-box;
  padding: 0.2rem 0.25rem;
  display: flex;
}
.visiting-card {
  width: 1.8rem;
  height: 1.08rem;
}
.visiting-card > img {
  width: 100%;
  height: 100%;
}
.intention {
  margin-left: 0.7rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: 0.24rem;
  color: #fff;
}
.intention-title {
  color: #00edff;
  margin-right: 0.35rem;
}

.course-down-tips {
  position: relative;
  padding-bottom: 0.3rem;
  text-align: center;
  color: #80afba;
}
.course-down-tips::before,
.course-down-tips::after {
  content: "";
  position: absolute;
  top: 0.2rem;
  width: 2.4rem;
  border-top: 1px solid #337e90;
}
.course-down-tips::before {
  left: 0;
}
.course-down-tips::after {
  right: 0;
}
</style>